@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.page {
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr min-content;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .page {
    font-size: 16px;
    line-height: 24px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 500;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}
.button {
  border: none;
  padding: 0 10px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 40px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.page-header {
  display: flex;
  padding: 15px 12px 10px 20px;
  align-items: center;
  justify-content: space-between;
  font-family: "Oswald", "Arial", sans-serif;
  z-index: 20;
}
@media (min-width: 768px) {
  .page-header {
    padding: 50px 110px;
    justify-content: flex-start;
  }
}
@media (min-width: 768px) {
  .page-header__logo {
    margin-right: 15px;
  }
}
@media (min-width: 768px) {
  .page-header__navigation {
    margin-left: auto;
  }
}
.page-main__section {
  padding: 0 20px;
}
.navigation__list {
  position: absolute;
  top: -200px;
  left: 0;
  display: flex;
  width: 100%;
  padding: 0;
  margin: 0;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
  background-color: #ffffff;
  z-index: 10;
  transition: 0.5s;
}
.navigation__list--opened {
  top: 65px;
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    flex-direction: row;
    gap: 35px;
    background-color: transparent;
  }
}
.navigation__item {
  width: 100vw;
  border-top: 1px solid #e6e6e6;
  text-align: center;
}
@media (min-width: 768px) {
  .navigation__item {
    border-top: none;
    width: auto;
  }
}
.navigation__link {
  display: block;
  padding: 22px 0;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  font-weight: 500;
  font-size: 20px;
  text-decoration: none;
  text-transform: uppercase;
}
.navigation__link:hover {
  opacity: 0.6;
}
.navigation__link:active {
  opacity: 0.3;
}
.navigation__link--active {
  text-decoration: underline 2px #68b738;
  text-underline-offset: 12px;
}
@media (min-width: 768px) {
  .navigation__link--active {
    text-decoration-color: inherit;
  }
}
@media (min-width: 1440px) {
  .navigation__link {
    color: #ffffff;
    font-weight: 400;
  }
}
.navigation-toggle {
  display: flex;
  flex-direction: column;
  height: 40px;
  width: 40px;
  padding: 0;
  padding-top: 8px;
  align-items: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation-toggle {
    display: none;
  }
}
.navigation-toggle__line {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.navigation-toggle__line::before {
  display: block;
  margin-top: 16px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  content: "";
  transition: 0.3s;
}
.navigation-toggle__line::after {
  display: block;
  margin-top: -10px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  content: "";
  transition: 0.3s;
}
.navigation-toggle--opened .navigation-toggle__line {
  background-color: transparent;
}
.navigation-toggle--opened .navigation-toggle__line::before {
  transform: translate3d(0, -3.5px, 0) rotate(45deg);
}
.navigation-toggle--opened .navigation-toggle__line::after {
  transform: translate3d(0, 4.5px, 0) rotate(-45deg);
}
.hero {
  position: relative;
  font-family: "Oswald", "Arial", sans-serif;
}
@media (max-width: 320px) {
  .hero {
    text-align: center;
    color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .hero {
    margin-bottom: 305px;
  }
}
.hero__background-wrapper {
  position: absolute;
  top: 0;
  width: 320px;
  min-height: 302px;
  background-image: url("../img/index-background-cat.jpg");
  background-position: center;
  background-size: 320px 302px;
  background-repeat: no-repeat;
  z-index: -1;
}
@media (min-width: 321px) and (max-width: 1439px) {
  .hero__background-wrapper {
    display: none;
  }
}
@media (min-width: 1440px) {
  .hero__background-wrapper {
    position: absolute;
    right: 0;
    top: -168px;
    width: 50%;
    background-image: url("../img/index-background-cat-desktop.jpg");
    background-size: cover;
    background-position: left;
    min-height: 694px;
  }
}
.hero__background-image {
  min-height: 302px;
  background-color: rgba(104, 183, 56, 0.85);
}
@media (min-width: 1440px) {
  .hero__background-image {
    min-height: 694px;
  }
}
.hero__title {
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .hero__title {
    max-width: 450px;
    margin-bottom: 40px;
  }
}
.hero__text {
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .hero__text {
    margin-bottom: 50px;
  }
}
.hero__button {
  padding: 0 44px;
}
@media (min-width: 768px) {
  .hero__button {
    align-self: flex-start;
    padding: 0 26px;
  }
}
.hero__image {
  display: block;
  width: 100%;
  max-width: 709px;
  height: auto;
  object-fit: cover;
}
@media (min-width: 1440px) {
  .hero__image {
    position: absolute;
    max-width: 552px;
    margin: 0 auto;
    left: 50%;
    top: 0;
    transform: translateX(-28%);
  }
}
.section__content {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 302px;
  padding: 28px 20px;
  padding-bottom: 0;
}
@media (max-width: 1439px) {
  .section__content {
    align-items: center;
  }
}
@media (min-width: 1440px) {
  .section__content {
    padding: 0 110px;
  }
}
.section__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
}
.social {
  display: flex;
  list-style-type: none;
}
.social__link {
  display: block;
  padding: 11px 10px;
}
.contact {
  padding: 0;
}
.contact__map {
  width: 100%;
}
@media (min-width: 768px) {
  .contact__map {
    height: 400px;
  }
}
.contact__intro {
  display: flex;
  align-items: center;
}
.page-footer {
  display: flex;
  padding: 35px 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.page-footer__developer {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  font-size: 16px;
  color: #444444;
  text-decoration: none;
}
.page-footer__developer:hover svg {
  color: #68b738;
}
.page-footer__developer:active {
  opacity: 0.3;
}
.target__list {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style-type: none;
  gap: 20px;
}
@media (min-width: 768px) {
  .target__list {
    gap: 30px;
  }
}
@media (min-width: 1440px) {
  .target__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 80px;
  }
}
.target__item {
  display: grid;
  padding: 20px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .target__item {
    padding: 60px 50px;
  }
}
@media (min-width: 768px) and (max-width: 1439px) {
  .target__item {
    grid-template-columns: repeat(2, 1fr);
  }
}
.target__name {
  display: flex;
  align-items: center;
}
.target__illustration {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 50px;
  margin-right: 20px;
  border-radius: 50%;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .target__illustration {
    width: 100px;
    height: 100px;
    margin-right: 60px;
  }
}
@media (min-width: 768px) and (max-width: 1439px) {
  .target__illustration {
    grid-column: 2 / 3;
  }
}
.target__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (min-width: 768px) {
  .target__icon {
    transform: translate(-50%, -50%) scale(2);
  }
}
.target__title {
  display: inline-block;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 37px;
  text-transform: uppercase;
}
.target__text {
  padding: 25px 0;
  margin-bottom: auto;
  border-bottom: 1px solid #d9d9d9;
}
@media (max-width: 768px) {
  .target__text {
    margin-bottom: 20px;
  }
}
@media (min-width: 768px) {
  .target__text {
    border-bottom: none;
  }
}
.target__button {
  display: flex;
  align-items: center;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  color: inherit;
  transition: 0.1s;
}
.target__button:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .target__button {
    font-size: 20px;
  }
}
.target__button::after {
  display: inline-block;
  width: 24px;
  height: 12px;
  margin-left: 15px;
  background-image: url("../img/arrow.svg");
  background-size: 24px 12px;
  background-position: center;
  background-repeat: no-repeat;
  content: "";
  transition: 0.3s;
}
@media (min-width: 768px) {
  .target__button::after {
    margin-left: 25px;
  }
}
.target__button:hover::after {
  width: 32px;
  background-size: 32px 12px;
}
.about__list {
  display: grid;
  row-gap: 20px;
  padding: 0;
  list-style-type: none;
}
.about__item {
  display: grid;
  align-items: center;
  grid-template-columns: 60px auto;
  gap: 20px;
}
.about__icon {
  width: 60px;
  height: 60px;
  background-color: #68b738;
}
